$hexColor: #F4E5E2;
.header-banner {
  width: 100%;
  height: 60px;
  background: url("/static/img/content/banner.png");
}
.header-content {
  position: relative;
}
.header-content__logo {
  float: left;
  margin-right: 85px;
}
.header-content__select {
  float: left;
    li {
      position: relative;
      padding: 8px 84px 8px 20px;
      border: 1px solid $hexColor;
      cursor: pointer;
      span {
        position: absolute;
        top: 13px;
        right: 13px;
        color: $hexColor;
        font-size: 14px;
      }
      &:hover {
        background: $hexColor;
      }
    }
}
.header-content__select li:first-child:hover span {
  color: #333;
}
.header-content__select:not(li:first-child) span {
  display: none;
}
.header-content__select li:first-child span {
  display: inline-block!important;
}
.header-content__select li:nth-child(2),
.header-content__select li:nth-child(3),
.header-content__select li:nth-child(4) {
  display: none;
}
.re-active {
  display: block;
}
.header-content__country-list {
  font-family: $latoFont;
  font-size: 14px;
  line-height: 17px;
  color: #333;
}
