html, body {
  height: 100%;
}

.page__wrapper {
  min-height: 100%;
  margin-bottom: -50px;
}

.container {
  width: 1138px;
  margin: 0 auto;
  border: 1px solid deeppink;
}

* html .page__wrapper {
  height: 100%;
}

.page__buffer {
  height: 50px;
}

.clearfix::after {
  position: absolute;
  display: table;
  content: "";
  clear: both;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

.header-banner {
  width: 100%;
  height: 60px;
  background: url("/static/img/content/banner.png");
}

.header-content {
  position: relative;
}

.header-content__logo {
  float: left;
  margin-right: 85px;
}

.header-content__select {
  float: left;
}

.header-content__select li {
  position: relative;
  padding: 8px 84px 8px 20px;
  border: 1px solid #F4E5E2;
  cursor: pointer;
}

.header-content__select li span {
  position: absolute;
  top: 13px;
  right: 13px;
  color: #F4E5E2;
  font-size: 14px;
}

.header-content__select li:hover {
  background: #F4E5E2;
}

.header-content__select li:first-child:hover span {
  color: #333;
}

.header-content__select:not(li:first-child) span {
  display: none;
}

.header-content__select li:first-child span {
  display: inline-block !important;
}

.header-content__select li:nth-child(2),
.header-content__select li:nth-child(3),
.header-content__select li:nth-child(4) {
  display: none;
}

.re-active {
  display: block;
}

.header-content__country-list {
  font-family: "Lato", sans-serif;
  font-size: 14px;
  line-height: 17px;
  color: #333;
}
